import { useState } from 'react';
import StepperWrapper from './StepperWrapper';

export const DefaultFormDataInt = {
  // tanTest: 'tan' | 'burn',
  // contrastTest
  pinkOrangeTest: 'pink' | 'orange',
  metalTest: 'gold' | 'silver',
  rgbTest: 'red' | 'blue' | 'yellow',
  // pokud studeny typ
  // podle vysledku svetle ruzova x fuchsia
  // cerna x seda sick/wash out
  // pokud teply tak hneda x tyrkysova
};

function Form({ img }) {
  const [step, setStep] = useState(0);
  const [errors, setErrors] = useState(false);
  const [hue, setHue] = useState('');
  const [contrast, setContrast] = useState('');
  const [croma, setCroma] = useState('');
  const [formData, setFormData] = useState(DefaultFormDataInt);

  function validate() {
    switch (step) {
      case 0:
        if (formData.pinkOrangeTest !== '') {
          return true;
        }
        return false;
      case 1:
        if (formData.metalTest !== '') {
          return true;
        }
        return false;
      case 2:
        if (formData.rgbTest !== '') {
          return true;
        }
        return false;
      default:
        return false;
    }
  }

  const view = () => {
    switch (step) {
      case 0:
        return <>hhh</>;
      case 1:
        return <>case 1</>;
      case 2:
        return <>case 2</>;
      case 3:
        return <>case 3</>;
      default:
        return <>case 1</>;
    }
  };

  function handleSubmit() {
    if (validate()) {
      setErrors(false);
      setStep(step + 1);
    } else {
      setErrors(true);
    }
    if (step === 3) {
      console.log(formData);
    }
  }

  function handleBack() {
    setStep(step - 1);
    setErrors(false);
  }

  return (
    <div className="formContainer">
      <StepperWrapper step={step + 1} />
      <h1>{step + 1}</h1>
      {view()}
      <div className="buttons">
        {step > 0 && (
          <button className="button-secondary back" onClick={handleBack}>
            Back
          </button>
        )}
        <button className="button-primary" onClick={handleSubmit}>
          {step < 3 ? 'Next' : 'Submit'}
        </button>
      </div>
    </div>
    // <div>{img ? <img src={img} alt="img" /> : null}</div>
  );
}

export default Form;
